.App {
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: flex-end;
  align-items: center;
  height: 100%;
}

.home-page {
  display: flex;
  justify-content: center;
  height: 85%;
  width: 90%;
  margin: 10% 0% 0% 5%;
}

.search-form-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 90%;
  width: 45%;
  margin-bottom: 2%;
}

.prescription-preview {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  height: 100%;
  width: 100%;
  border: 3px solid black;
  border-radius: 10px;
  background-color: white;
}

.instructions-container {
  display: flex;
  justify-content: center;
  height: 50%;
  width: 25%;
  margin: 10% 0% 0% 5%;
  border: 3px solid black;
  border-radius: 10px;
  background-color: white;
}
